<template>
  <div class="product">
    <div class="row">
      <form class="search d-flex mt-2 mb-4">
        <input
          class="form-control"
          type="search"
          placeholder="Search"
          aria-label="Search"
          v-model="searchWords"
        />
        <button class="btn" type="submit" @click.prevent="filterSearch">
          <font-awesome-icon :icon="['fas', 'search']" size="lg" class="icon" />
        </button>
      </form>
      <div
        class="col-sm-3 item"
        v-for="product in filterSearch()"
        :key="product.id"
      >
        <router-link
          :to="{ name: 'product-detail', params: { id: product.id } }"
        >
          <img :src="product.image" alt="" />
          <div class="item-detail mt-2">
            <p class="item-name">{{ product.title }}</p>
            <p class="item-price">US$ {{ product.price }}</p>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "ProductList",
  props: {
    products: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      product: this.products,
      searchWords: "",
    };
  },
  methods: {
    filterSearch() {
      return this.products.filter((result) =>
        result.title.toLowerCase().includes(this.searchWords.toLowerCase())
      );
    },
  },
};
</script>

